export default {
	data() {
		return {
			WinList:this.$WinList,
			picker: this.$GambleList,
			modalName: null,
			lstIndex: -1
		};
	},
	onShow() {
		//console.log('系统记录'+this.$WinList[1].LstAddress);
		
		//this.WinList=this.$WinList
		//console.log('页面记录'+this.WinList[1].LstMoney);
	},
	methods: {
		on_edit(e) {
			this.lstIndex = e.currentTarget.dataset.index;
			uni.navigateTo({
				url: '/pages/index/edit?pid=' + this.lstIndex
			});

			console.log(this.lstIndex);
		},
		on_del(e) {
			this.lstIndex = e.currentTarget.dataset.index;
			//console.log(e.currentTarget.dataset.index);
			this.WinList.splice(this.lstIndex, 1);
			//this.$WinList = this.WinList;
			try {
				uni.setStorageSync('keyWinList', this.WinList);
				try {
					this.$WinList = uni.getStorageSync('keyWinList');
					uni.showToast({
						title: '删除成功',
						duration: 2000
					});
					this.WinList = this.$WinList;
				} catch (e) {
					uni.showToast({
						title: '删除失败',
						duration: 2000
					});
					this.WinList = this.$WinList;
				}
			} catch (e) {
				uni.showToast({
					title: '删除失败',
					duration: 2000
				});
				this.WinList = this.$WinList;
			}
			this.modalName = null;
		},
		showModal(e) {
			this.modalName = 'DelDialog';
			this.lstIndex = e.currentTarget.dataset.index;
		},
		hideModal(e) {
			this.modalName = null;
		},
		addwin(type, money) {
			if (type == 1 && money > 0) {
				return '赢';
			}
			if (type == 2 && money > 0) {
				return '输';
			}
			if ((type == 2 || type == 1) && money == 0) {
				return '平';
			}
		},
		addclass(id, type, money) {
			if (id == 1) {
				if (type == 1 && money > 0) {
					return 'bg-orange';
				}else if (type == 2 && money > 0) {
					return 'bg-cyan';
				}else if (type == 2  && money == 0) {
					return 'bg-grey';
				}else if(type == 1  && money == 0) {
					return 'bg-grey';
				}else{
					return '';
				}
				
			} else {
				if (type == 1 && money > 0) {
					return 'line-orange';
				}else if (type == 2 && money > 0) {
					return 'line-cyan';
				}else if (type == 2  && money == 0) {
					return 'line-grey';
				}else  if(type == 1  && money == 0) {
					return 'line-grey';
				}else{
					return '';
				}
			}
		}
	}
};
